"use client";

export default function Testimonials() {
  return (
    <section className="py-16 px-6">
      <div className="max-w-5xl mx-auto text-center">
        <h3 className="text-2xl mb-6 font-bold">Loved by Millions</h3>
        <div className="grid gap-8 md:grid-cols-3">
          {[
            {
              name: "John Doe",
              text: "I use NordVPN daily to access content and secure my data. Absolute peace of mind!",
            },
            {
              name: "Sarah Lee",
              text: "The threat protection feature is worth every penny. No ads, no malware.",
            },
            {
              name: "Michael S.",
              text: "Compared to other VPNs, NordVPN always stands out in performance and features.",
            },
          ].map((t, idx) => (
            <div key={idx} className="bg-gray-800 p-6 rounded-lg shadow-lg">
              <p className="italic opacity-80">“{t.text}”</p>
              <h4 className="mt-4 font-semibold text-blue-400">— {t.name}</h4>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
